import { Card, Grid, styled, useTheme, Box, Button, Menu, MenuItem, LinearProgress, TextField, Icon, CircularProgress } from '@mui/material';
import { Fragment, useEffect, useState } from 'react';
import Campaigns from './shared/Campaigns';
import DoughnutChart from './shared/Doughnut';
import RowCards from './shared/RowCards';
import StatCards from './shared/StatCards';
import StatCards2 from './shared/StatCards2';
import TopSellingTable from './shared/TopSellingTable';
import UpgradeCard from './shared/UpgradeCard';
import SimpleTable from "./SimpleTable";
import NumberConsole from 'app/components/NumberConsole'
import useSettings from 'app/hooks/useSettings';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useSearchParams } from "react-router-dom";
import Parse from 'app/vendor/Parse'
import { useDispatch, useSelector } from 'react-redux';
import KeyPad from 'app/components/KeyPad'
import Search from 'app/components/Search'
import Filter from "app/components/Filter"
import { setApplicationMessage } from 'app/redux/actions/ApplicationActions'
import { NumberCategory } from 'app/../../shared/NumberCategory';

const Component = () => {

    const { updateSettings } = useSettings()
    const [data, setData] = useState([])
    const [queriedDate, setQueriedData] = useState([])
    const [isLoading, setIsLoading] = useState(false)
    const [selectedIndex, setSelectedIndex] = useState(0)
    const [query, setQuery] = useState('')
    const [cat, setSelectedCat] = useState(NumberCategory[selectedIndex])
    const dispatch = useDispatch()
    const search = async (query) => {
        setIsLoading(true)
        const response = await Parse.Cloud.run('searchResellerNumbers', {
            query
        })
        if (response?.message) {
            dispatch(setApplicationMessage({
                text: response?.message
            }))
        } else {
            setData(response)
        }
        setIsLoading(false)
    }
    const headline1 = <h1 style={{ textAlign: 'center', color: '#ff0000' }}>Mobile413 Reseller Program</h1>

    useEffect(() => {
        const newData = data
            .filter(e => cat.func(e.num))
            .filter(e => e.num.includes(query))
        setQueriedData(newData)
    }, [data, query, selectedIndex])



    const renderStartContent = () => {
        return <Box display="flex" alignContent="center" justifyContent="center" alignItems="center">
            <Filter
                handleMenuItemClick={(a, b, cat) => { setSelectedIndex(b); setSelectedCat(cat) }}
                selectedIndex={selectedIndex}
                list={NumberCategory}
            />
            <br />
            <br />
        </Box>
    }
    return (
        <>
            <Search
                onSearch={(query) => search((''))}
                onQuery={q => setQuery(q)}
                isLoading={isLoading}
                data={queriedDate}
                headline1={headline1}
                startContent={renderStartContent()}
                displaySource={true}
                pageSize={24}
                hideCopy={false}
                hideDl={false}
            />

        </>
    );
};

export default Component;
